.form-container h1 {
    font-size: 18px;
    font-weight: bold;
    margin: 10px 0 0 0;
}

.form-container > * {
    width: 100%
}

label span {
    margin: 0 5%;
    font-weight: bold;
}

.input-group {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
}

.input-group input {
    width: 45%;
}

